import React, { Component } from 'react';
import './card.css';
import { Grid, Cell, Card, CardTitle, CardText, CardActions, Button } from 'react-mdl';

class CardJS extends Component {
    render() {
        return(
                <div className="project-grid">
                <Grid>

                {/* Projekt numer 1 JS */}
                 <Cell col={6} tablet={12}>
                    <Card shadow={5} style={{width: '100%', height: '340px'}}>
                        <CardTitle style={{color: '#fff', height: '176px', background: 'url(https://rpodraza.pl/img/react-img/myprojects/todo.png) center / cover'}}>
                        </CardTitle>

                        <CardText>
                        Lista zadań wykonana w JavaScript. Tworząc ten projekt poduczyłem się zarządzania funkcjami oraz przekazywania ich parametrów.  
                        </CardText>

                        <CardActions border>
                        <a href="https://rpodraza.pl/demo11/" target="_blank" rel="noopener noreferrer"><Button colored>DEMO</Button></a>
                        <a href="https://github.com/rafi9898/TO-DO-Lista" target="_blank" rel="noopener noreferrer"><Button colored>Github</Button></a>
                        </CardActions>
                    </Card>
                    </Cell>


                     {/* Projekt numer 2 JS */}
                 <Cell col={6} tablet={12}>
                    <Card shadow={5} style={{width: '100%', height: '340px'}}>
                        <CardTitle style={{color: '#fff', height: '176px', background: 'url(https://rpodraza.pl/img/react-img/myprojects/pianino.png) center / cover'}}>
                        </CardTitle>

                        <CardText>
                        Aplikacji stworzona w języku JavaScript. Po wciśnięciu odpowiednich klawiszy nasza aplikacja odtwarza dźwięki pianina.
                        </CardText>

                        <CardActions border>
                        <a href="https://rpodraza.pl/demo13/" target="_blank" rel="noopener noreferrer"><Button colored>DEMO</Button></a>
                        <a href="https://github.com/rafi9898/Pianino" target="_blank" rel="noopener noreferrer"><Button colored>Github</Button></a>
                        </CardActions>
                    </Card>
                    </Cell>


                    {/* Projekt numer 3 JS */}
                 <Cell col={6} tablet={12}>
                    <Card shadow={5} style={{width: '100%', height: '340px'}}>
                        <CardTitle style={{color: '#fff', height: '176px', background: 'url(https://rpodraza.pl/img/react-img/myprojects/kalkulator.png) center / cover'}}>
                        </CardTitle>

                        <CardText>
                        Kalkulator wykonany w języku Javascript. Kalkulator przypomina nam starszy model telefonu komórkowego firmy Nokia.   
                        </CardText>

                        <CardActions border>
                        <a href="https://github.com/rafi9898/Kalkulator-JavaScript" target="_blank" rel="noopener noreferrer"><Button colored>DEMO</Button></a>
                        <a href="https://github.com/rafi9898/Kalkulator-JavaScript" target="_blank" rel="noopener noreferrer"><Button colored>Github</Button></a>
                        </CardActions>
                    </Card>
                    </Cell>


                     {/* Projekt numer 4 JS */}
                 <Cell col={6} tablet={12}>
                    <Card shadow={5} style={{width: '100%', height: '340px'}}>
                        <CardTitle style={{color: '#fff', height: '176px', background: 'url(https://rpodraza.pl/img/react-img/myprojects/gitapi.png) center / cover'}}>
                        </CardTitle>

                        <CardText>
                        Wyszukiwarka użytkowników mających konto na znanej platformie GitHub. Do stworzenia aplikacji wykorzystałem JavaScript + API  
                        </CardText>

                        <CardActions border>
                        <a href="https://rpodraza.pl/demo3/" target="_blank" rel="noopener noreferrer"><Button colored>DEMO</Button></a>
                        <a href="https://github.com/rafi9898/Wyszukiwarka-GitHub" target="_blank" rel="noopener noreferrer"><Button colored>Github</Button></a>
                        </CardActions>
                    </Card>
                    </Cell>

                     {/* Projekt numer 5 JS */}
                 <Cell col={6} tablet={12}>
                    <Card shadow={5} style={{width: '100%', height: '340px'}}>
                        <CardTitle style={{color: '#fff', height: '176px', background: 'url(https://rpodraza.pl/img/react-img/myprojects/kurswalut.png) center / cover'}}>
                        </CardTitle>

                        <CardText>
                        Aplikacja została napisana w języku javascript z wykorzystaniem Fetch Api. Zadaniem aplikacji jest pokazywanie najnowszego kursu kryptowaluty Bitcoin.   
                        </CardText>

                        <CardActions border>
                        <a href="https://rpodraza.pl/demo2/" target="_blank" rel="noopener noreferrer"><Button colored>DEMO</Button></a>
                        <a href="https://github.com/rafi9898/Kurs-Bitcoina" target="_blank" rel="noopener noreferrer"><Button colored>Github</Button></a>
                        </CardActions>
                    </Card>
                    </Cell>

                    
                     {/* Projekt numer 6 JS */}
                 <Cell col={6} tablet={12}>
                    <Card shadow={5} style={{width: '100%', height: '340px'}}>
                        <CardTitle style={{color: '#fff', height: '176px', background: 'url(https://rpodraza.pl/img/react-img/myprojects/graKosci.png) center / cover'}}>
                        </CardTitle>

                        <CardText>
                        Interaktywna gra multiplayer w kości. Gre napisałem w celu doskonalenia swoich umiejętności z zakresu Jquery. Do stworzenia gry wykorzystałem technologie HTML/CSS/JAVASCRIPT/JQUERY.  
                        </CardText>

                        <CardActions border>
                        <a href="https://rpodraza.pl/demo9/" target="_blank" rel="noopener noreferrer"><Button colored>DEMO</Button></a>
                        <a href="https://github.com/rafi9898/Gra-Kosci" target="_blank" rel="noopener noreferrer"><Button colored>Github</Button></a>
                        </CardActions>
                    </Card>
                    </Cell>
                </Grid>
                </div>
                
        );
    }
}

export default CardJS;


