import React, { Component } from 'react';
import './card.css';
import { Grid, Cell, Card, CardTitle, CardText, CardActions, Button } from 'react-mdl';

class CardPHP extends Component {
    render() {
        return(
                <div className="project-grid">
                <Grid>

                {/* Projekt numer 1 PHP */}
                 <Cell col={6} tablet={12} mobile={12}>
                    <Card shadow={5} style={{width: '100%', height: '340px'}}>
                        <CardTitle style={{color: '#fff', height: '176px', background: 'url(https://rpodraza.pl/img/react-img/myprojects/mybookmakrs.png) center / cover'}}>
                        </CardTitle>

                        <CardText>
                        Strona umożliwiające dodawanie zakładek do bazy danych gdzie w łatwy i szybki sposób możesz mieć do nich dostęp. Do stworzenia systemu wykorzystałem takie technologie jak : HTML5, CSS, JavaScript, BootStrap, jQuery, PHP oraz MYSQL.  
                        </CardText>

                        <CardActions border>
                        <a href="http://mybookmarks.eu" target="_blank" rel="noopener noreferrer"><Button colored>DEMO</Button></a>
                        <a href="https://github.com/rafi9898/System-Zakladek" target="_blank" rel="noopener noreferrer"><Button colored>Github</Button></a>
                        </CardActions>
                    </Card>
                    </Cell>


                     {/* Projekt numer 2 PHP */}
                 <Cell col={6} tablet={12} mobile={12}>
                    <Card shadow={5} style={{width: '100%', height: '340px'}}>
                        <CardTitle style={{color: '#fff', height: '176px', background: 'url(https://rpodraza.pl/img/react-img/myprojects/sklep.png) center / cover'}}>
                        </CardTitle>

                        <CardText>
                        Skrypt został zintegrowany z dwoma systemami płatności takimi jak:  paypal oraz microsms (płatności sms). Umożliwia sprzedaż skryptu. 
                        </CardText>

                        <CardActions border>
                        <a href="https://jackpot.rpodraza.pl/" target="_blank" rel="noopener noreferrer"><Button colored>DEMO</Button></a>
                        <a href="https://github.com/rafi9898/Mini-Sklep-Online" target="_blank" rel="noopener noreferrer"><Button colored>Github</Button></a>
                        </CardActions>
                    </Card>
                    </Cell>
                </Grid>
                </div>
                
        );
    }
}

export default CardPHP;


