import React, { Component } from 'react';
import './card.css';
import { Grid, Cell, Card, CardTitle, CardText, CardActions, Button } from 'react-mdl';

class CardREACT extends Component {
    render() {
        return(
                <div className="project-grid">
                <Grid>

                {/* Projekt numer 1 REACT */}
                 <Cell col={12} tablet={12}>
                    <Card shadow={5} style={{width: '90%', height: '340px'}}>
                        <CardTitle style={{color: '#fff', height: '176px', background: 'url(https://rpodraza.pl/img/react-img/myprojects/portfolio.png) center / cover'}}>
                        </CardTitle>

                        <CardText>
                        Pierwsza strona/projekt wykonany w technologii React. Do stworzenia portfolio wykorzystałem moduł react-router-dom. 
                        </CardText>

                        <CardActions border>
                        <a href="http://rpodraza.pl" target="_blank" rel="noopener noreferrer"><Button colored>DEMO</Button></a>
                        <a href="https://github.com/rafi9898/System-Zakladek" target="_blank" rel="noopener noreferrer"><Button colored>Github</Button></a>
                        </CardActions>
                    </Card>
                    </Cell>
                </Grid>
                </div>
                
        );
    }
}

export default CardREACT;


