import React, { Component } from 'react';
import './card.css';
import { Grid, Cell, Card, CardTitle, CardText, CardActions, Button } from 'react-mdl';

class CardHTML extends Component {
    render() {
        return(
                <div className="project-grid">
                <Grid>

                {/* Projekt numer 1 HTML */}
                 <Cell col={6} tablet={12} mobile={12}>
                    <Card shadow={5} style={{width: '100%', height: '340px'}}>
                        <CardTitle style={{color: '#fff', height: '176px', background: 'url(https://rpodraza.pl/img/react-img/myprojects/Layout1.png) center / cover'}}>
                        </CardTitle>

                        <CardText>
                            Layout wykonany dla strony PiggyBank. Do wykonania szablonu wykorzystałem technologie takie jak HTML/CSS/Bootstrap. Jest to jedna z moich pierwszych prac. 
                        </CardText>

                        <CardActions border>
                        <a href="https://rpodraza.pl/demo12/" target="_blank" rel="noopener noreferrer"><Button colored>DEMO</Button></a>
                        <a href="https://github.com/rafi9898/PiggyBank-Layout" target="_blank" rel="noopener noreferrer"><Button colored>Github</Button></a>
                        </CardActions>
                    </Card>
                    </Cell>


                     {/* Projekt numer 2 HTML */}
                 <Cell col={6} tablet={12} mobile={12}>
                    <Card shadow={5} style={{width: '100%', height: '340px'}}>
                        <CardTitle style={{color: '#fff', height: '176px', background: 'url(https://rpodraza.pl/img/react-img/myprojects/Layout2.png) center / cover'}}>
                        </CardTitle>

                        <CardText>
                        Layout wykonany w celach doskonalenia swoich umiejętności z zakresu HTML/CSS/Bootstrap. Jest to jedna z moich pierwszych prac.
                        </CardText>

                        <CardActions border>
                        <a href="https://rpodraza.pl/demo14/" target="_blank" rel="noopener noreferrer"><Button colored>DEMO</Button></a>
                        <a href="https://github.com/rafi9898/Layout1" target="_blank" rel="noopener noreferrer"><Button colored>Github</Button></a>
                        </CardActions>
                    </Card>
                    </Cell>


                    {/* Projekt numer 3 HTML */}
                 <Cell col={6} tablet={12} mobile={12}>
                    <Card shadow={5} style={{width: '100%', height: '340px'}}>
                        <CardTitle style={{color: '#fff', height: '176px', background: 'url(https://rpodraza.pl/img/react-img/myprojects/rekstyle.png) center / cover'}}>
                        </CardTitle>

                        <CardText>
                        Projekt strony internetowej wykonany dla firmy zajmującej się sprzedażom reklam. Do stworzenia strony wykorzystałem HTML5, CSS, JavaScript oraz jQuery.  
                        </CardText>

                        <CardActions border>
                        <a href="http://rekstyle.pl" target="_blank" rel="noopener noreferrer"><Button colored>DEMO</Button></a>
                        <a href="https://github.com/rafi9898/Projekt-Strony-Rekstyle" target="_blank" rel="noopener noreferrer"><Button colored>Github</Button></a>
                        </CardActions>
                    </Card>
                    </Cell>


                     {/* Projekt numer 4 HTML */}
                 <Cell col={6} tablet={12} mobile={12}>
                    <Card shadow={5} style={{width: '100%', height: '340px'}}>
                        <CardTitle style={{color: '#fff', height: '176px', background: 'url(https://rpodraza.pl/img/react-img/myprojects/fryzjer.png) center / cover'}}>
                        </CardTitle>

                        <CardText>
                        Strona wykonana specjalnie na zamówienie klienta. Technologię jakie wykorzystałem do jej stworzenia to HTML/CSS/Javascript/Bootstrap. Strona jest responsywna.  
                        </CardText>

                        <CardActions border>
                        <a href="https://rpodraza.pl/demo10/" target="_blank" rel="noopener noreferrer"><Button colored>DEMO</Button></a>
                        <a href="https://rpodraza.pl" target="_blank" rel="noopener noreferrer"><Button colored>Prywatne</Button></a>
                        </CardActions>
                    </Card>
                    </Cell>
                </Grid>
                </div>
                
        );
    }
}

export default CardHTML;


