import React, { Component } from 'react';
import { Grid, Cell, Button } from 'react-mdl';
import Footer from './footer';

class Home extends Component {
    render() {
        return(
            <div className="home">
            <div className="overflow">
                <Grid className="flow">
                    <Cell className="InfoBox" col={12}>
                        <Grid>

                            <Cell col={12} className="titleBox">
                                <h2>O Mnie</h2>
                            </Cell>

                            <Cell className="myphoto" col={4} tablet={12}>
                                <img src="https://rpodraza.pl/img/react-img/myphoto.png" alt="Moje zdjęcie" />
                            </Cell>

                            <Cell className="info" col={8} tablet={12}>
                                <p>Witaj!</p>
                                <p>Nazywam się Rafał i zapraszam Cię do zapoznania się z moim portfolio.</p>
                                <p>W tym roku ukończyłem technikum o profilu informatycznym. Od ponad roku zajmuje się nauką tworzenia stron i aplikacji internetowych. Codziennie poświęcam czas na poznawanie nowych zagadnień i tworzenie projektów, które publikuje na serwisie GitHub.</p>
                                <p>Aktualnie moim celem jest znalezienie pierwszego stażu, który umożliwiłby mi zdobyć doświadczenie zawodowe oraz poszerzyć swoją wiedzę.</p>
                                <a href="https://rpodraza.pl/moje-cv.pdf" target="_blank" rel="noopener noreferrer"> 
                                <Button raised colored>ZOBACZ MOJE CV</Button>
                                </a>
                            </Cell>
                        </Grid>
                    </Cell>
                </Grid>
                </div>
                <Footer />
            </div>
        );
    }
}

export default Home;


