import React, { Component } from 'react';
import { Button } from 'react-mdl';
import './contact.css';
import Footer from './footer';

class Contact extends Component {
    render() {
        return(
            <div>
            <div className="contact-form">
            <div className="container-form">
            <div className="formbox">
            <h2>Formularz Kontaktowy</h2>
            <form method="post" action="https://formspree.io/r-podraza@wp.pl">
                <input placeholder="Imię..." required type="text" name="name" /> <br />
                <input placeholder="Adres Email..." required type="email" name="_replyto" /><br />
                <input placeholder="Numer Telefonu..." type="number" name="_subject" /><br />
                <textarea name="message" placeholder="Treść wiadomości..." required defaultValue={""} /><br />
                <Button type="reset" raised colored>Wyczyść</Button>
                <Button raised colored>Wyślij</Button>
            </form>
            </div>
            </div>
            </div>
            <Footer />
            </div>
        );
    }
}

export default Contact;


