import React, { Component } from 'react';
import './aboutme.css';
import { Grid, Cell } from "react-mdl";
import Footer from './footer';

class AboutMe extends Component {
    render() {
        return(
            <div className="aboutme">
                <Grid className="myskills">
                    <Cell col={3} phone={6}>
                        <img className="html5" src="https://rpodraza.pl/img/react-img/html5.png" alt="HTML5" />
                        <Grid>
                            <Cell col={12}>
                                <p>Dobra znajomość tecnologii HTML5</p>
                            </Cell>
                        </Grid>
                    </Cell>

                    <Cell col={3} phone={6}>
                        <img className="css" src="https://rpodraza.pl/img/react-img/css.png" alt="CSS3" />
                        <Grid>
                            <Cell col={12}>
                                <p>Średnia znajomość tecnologii CSS3</p>
                            </Cell>
                        </Grid>
                    </Cell>

                    <Cell col={3} phone={6}>
                        <img className="javascript" src="https://rpodraza.pl/img/react-img/javascript.png" alt="Javascript" />
                        <Grid>
                            <Cell col={12}>
                                <p>Podstawowa znajomość tecnologii JavaScript</p>
                            </Cell>
                        </Grid>
                    </Cell>

                    <Cell col={3} phone={6}>
                        <img className="ajax" src="https://rpodraza.pl/img/react-img/ajax.png" alt="Ajax" />
                        <Grid>
                            <Cell col={12}>
                                <p>Podstawowa znajomość tecnologii Ajax</p>
                            </Cell>
                        </Grid>
                    </Cell>

                    <Cell col={3} phone={6}>
                        <img className="jquery" src="https://rpodraza.pl/img/react-img/jquery.png" alt="Jquery" />
                        <Grid>
                            <Cell col={12}>
                                <p>Podstawowa znajomość biblioteki JQuery</p>
                            </Cell>
                        </Grid>
                    </Cell>

                    <Cell col={3} phone={6}>
                        <img className="bootstrap" src="https://rpodraza.pl/img/react-img/bootstrap.png" alt="Bootstrap" />
                        <Grid>
                            <Cell col={12}>
                                <p>Dobra znajomość tecnologii Bootstrap</p>
                            </Cell>
                        </Grid>
                    </Cell>

                    <Cell col={3} phone={6}>
                        <img className="less" src="https://rpodraza.pl/img/react-img/less.png" alt="Less" />
                        <Grid>
                            <Cell col={12}>
                                <p>Podstawowa znajomość preprocesora Less</p>
                            </Cell>
                        </Grid>
                    </Cell>

                    <Cell col={3} phone={6}>
                        <img className="react" src="https://rpodraza.pl/img/react-img/react.png" alt="React" />
                        <Grid>
                            <Cell col={12}>
                                <p>W trakcie nauki biblioteki React</p>
                            </Cell>
                        </Grid>
                    </Cell>
                </Grid>
                <Footer />
            </div>
        );
    }
}

export default AboutMe;


